import React,{Component} from 'react';
import './restodetail.css';
import {Link,NavLink,Route} from 'react-router-dom';
import Info from './Info';
import Rating from './Ratings';
import restaurant from './assets/restaurant.png';

class Restodetails extends Component{
	constructor({match}){
		super()
		this.state={
			toggle:true,
            rateclassstyle:"",
            infoclassstyle:"fortoggle",
			data:[],
			loaded:false,
			resId:match.params.num,

		}
  this.forsetratestates=forsetratestates.bind(this); 
  this.forsetinfostates=forsetinfostates.bind(this);  


function forsetratestates(){
  this.setState({toggle:false});
  this.setState({rateclassstyle:"fortoggle"});
  this.setState({infoclassstyle:""});
  }
  function forsetinfostates(){
  this.setState({toggle:true});
  this.setState({rateclassstyle:""});
  this.setState({infoclassstyle:"fortoggle"});
  }
	}
	 componentDidMount(){
  fetch(`https://developers.zomato.com/api/v2.1/restaurant?res_id=${this.state.resId}`,{
    method:'Get',
    headers:{
      'user-key':'bbf50d58793fbcdcfd09810ef8530da1',
      'Content-Type':'application/json',
      'accept':'application/json'
    }
  }).then(response=>response.json()).then(datas=>{
    console.log(datas);
    this.setState({data:this.state.data=datas});
    this.setState({loaded:this.state.loaded=true});
    
  })

}
	render(){
		console.log(this.state.data);
		return(
     <div className="restodetail">
    <div className="restoheader">
    <br/><br/><br/>
    <div className="forheaderthumb">
    <section>
    <img src={this.state.data.thumb} style={{width:"80px",height:"80px",borderRadius:"50px"}}/>
    </section>
    <section className="forheadertext">
    <h3>{this.state.data.name}</h3>
    <h5 style={{marginLeft:"-5px"}}></h5>
    </section>
    </div>
    </div>
    <div className="restocontent">
    <div className="forrateandinfo">
    <div className="forlink">
    <ul>

    <section className={this.state.infoclassstyle}>
    <li onClick={()=>this.forsetinfostates()}>INFO</li>
    </section>
    <section className={this.state.rateclassstyle}>
    <li onClick={()=>this.forsetratestates()}>RATINGS</li>
    </section>
    </ul>
    </div>
 <div className="linkscontents">
 {this.state.toggle?

 <div className="forInfo">
<section>
<div>
<label>Name:</label>
</div>
<div>
<p>{this.state.data.name}</p>
</div>
</section>
<section>
<div>
<label>PHONE:</label>
</div>
<div >
<p>{this.state.data.phone_numbers}</p>
</div>
</section>
<section>
<div>
<label>WEBSITE:</label>
</div>
<div className="foritems">
<p style={{fontSize:"15px"}}></p>
</div>
</section>
<section>
<div>
<label>LOCATION:</label>
</div>
<div className="foritems">
<p>{this.state.data.city}</p>
</div>
</section>
<section>
<div>
<label>ONLINE DELIVERY:</label>
</div>
<div className="foritemsdandb">
<p>{this.state.data.has_online_delivery}</p>
</div>
</section>
<section>
<div>
<label>TABLE 
BOOKING:</label>
</div>
<div className="foritemsdandb">
<p>{this.state.data.has_table_booking}</p>
</div>
</section>
<section>
<div>
<label>CUISINES:</label>
</div>
<div className="foritems">
<p>{this.state.data.cuisines}</p>
</div>
</section>
<br/>
<section>
<div>
<label>CURRENCY:</label>
</div>
<div className="foritems">
<p>{this.state.data.currency}</p>
</div>
</section>
<section>
<div>
<label>PRICE RANGE:</label>
</div>
<div className="foritems">
<p>{this.state.data.price_range}</p>
</div>
</section>
 </div>

 :
 <div className="forRating">
<ul type="circle">
<li><h3>{this.state.data.user_rating.rating_text}</h3>
<h5>{this.state.data.user_rating.aggregate_rating} stars</h5>
<p></p>
</li>
<br/>

</ul>
 </div>
}
    <Info/>
 </div>
    </div>
    <div className="formenu">
    <section className="formenutitle">
    <p>Menu</p>
    </section>
    </div>
    </div>
     </div>


			);
	}
}

export default Restodetails