import React,{Component} from 'react'
import './search.css'
import background from'./assets/background.png'
import {NavLink} from 'react-router-dom';
class Search extends Component{

constructor(){
	super()
this.state={
	inputdata:""
}	

}
render(){
	console.log(this.state.inputdata);
	return(

<div className="forsearch" >
<br/><br/>
<div className="searcholder">
<div className="fortitle">
<h2>Find nearby restaurants</h2>
</div>
<div className="searchinput">
<div className="forinputtype">
<input type="text" placeholder="Search restaurants" value={this.state.inputdata} onChange={e=>this.setState({inputdata:e.target.value})}/>
</div>
<div className="searchbutton">
<button type="button"><NavLink to={`search/${this.state.inputdata}`} className="Link">search</NavLink></button>
</div>
</div>
</div>
 </div>


);
}
}
export default Search