import React,{Component} from 'react'
import './nav.css'
import PODZO from './assets/PODZO.png'
import {Link,NavLink} from 'react-router-dom';
class Nav extends Component{
	constructor(){
		super()
		this.state={

		}
	}
 render(){
 	return(
    
        <div className="fornav">

        <div className="forlog">
        <img src={PODZO} className="forlog"/>
        </div>

        <div className="formenus">
        <ul>
       <li><NavLink to="/" className="link" activeClassName="linkactive">Home</NavLink></li>
       <li><NavLink to="/restaurantlist" className="link"  activeClassName="linkactive">Restaurants</NavLink></li>
       <li><NavLink to="#" className="link">Contact</NavLink></li>
       <li>|</li>
       <li><Link  className="link">Login</Link></li>
       <li ><button 
       style={{
       	background:"#ff6000",border:"1px solid #ff6000",padding:"10px",height:"35px",
       width:"150px",
       margin:"-10px",
       color:"white",
       borderRadius:"5px"
      }}>
       Sign up</button></li>
        </ul>
        </div>

        </div>

 		);
 }	
}
export default Nav