import React,{Component} from 'react';
import './searchdetail.css';
import './restaurant.css';
import './search.css'
import {Link,NavLink,Route} from 'react-router-dom';

class Searchresult extends Component{
	constructor({match}){
		super()
		this.state={
			toggle:true,
			data:[],
			loaded:false,
			resultshown:"",
			city:match.params.input

		}
	}
	 componentDidMount(){
fetch(`https://developers.zomato.com/api/v2.1/search?entity_id=280&entity_type=${this.state.city}`,{
    method:'Get',
    headers:{
      'user-key':'bbf50d58793fbcdcfd09810ef8530da1',
      'Content-Type':'application/json',
      'accept':'application/json'
    }
  }).then(response=>response.json()).then(datas=>{
    console.log(datas);
    this.setState({data:this.state.data=datas.restaurants});
    this.setState({loaded:this.state.resultshown=datas.results_shown});
    
  })
}
	render(){
		return(
     <div className="restodetail">
    <div className="searchheader">
    <br/><br/><br/>
 <div className="searcholder">
<div className="fortitle">
<h2>{this.state.resultshown} found restaurants</h2>
</div>
<div className="searchinput">
<div className="forinputtype">
  <input type="text" placeholder="Search restaurants"/>
</div>
<div className="searchbutton">
<button type="button" onClick={()=>alert('search')}>search</button>
</div>
</div>
</div>
    </div>

    <div className="serestaurantlist">
            <br/>
            <h1>Our restaurants</h1>
            <br/>
            {this.state.loaded?
             <div className="forcards">
               {this.state.data.map((item,index)=>{
                return(
                <div className="card"key={index}>
                <NavLink to="/details/18706428" className="Link">
             <div className="img">
            <img src={item.restaurant.thumb}/>

            
             </div>
             <div className="info">
             <p>{this.state.data.id}</p>
             <section className="forthumb">
             <img src={item.restaurant.thumb} style={{width:"50px",height:"50px",borderRadius:"50px"}}/>
             </section>
             <section className="fordesc">
             <h2>{item.restaurant.name}</h2>
             <p >$$$ - {item.restaurant.location.city}</p>
             </section>
               
             </div>
             </NavLink>
             </div>
                )
               })}
             </div>

              :<p>"loading..."</p>}
             
        <div className="exploremore">
      
        </div>     
            </div>
     </div>


			);
	}
}

export default Searchresult