import React,{Component} from 'react';
import './restaurant.css';
import food from './assets/food.png';
import {Link,NavLink} from 'react-router-dom';
import restaurant from "./assets/restaurant.png"
class Restaurant extends Component{
	constructor(){
		super()
		this.state={
			data:[1,2,3,4,5],
      loaded:false
		}
	}
  componentDidMount(){
  fetch("https://developers.zomato.com/api/v2.1/search?entity_id=280&entity_type=city&count=5",{
    method:'Get',
    headers:{
      'user-key':'bbf50d58793fbcdcfd09810ef8530da1',
      'Content-Type':'application/json',
      'accept':'application/json'
    }
  }).then(response=>response.json()).then(datas=>{
    console.log(datas);
    this.setState({data:this.state.data=datas.restaurants});
    this.setState({loaded:this.state.loaded=true});
    
  })
  } 
	render(){
    console.log(this.state.data);
    console.log(this.state.data.id);
   
		return(

            <div className="restaurantlist">
            <br/>
            <h1>Our restaurants</h1>
            <br/>
            {this.state.loaded?
             <div className="forcards">
               {this.state.data.map((item,index)=>{
                return(
                <div className="card"key={index}>
                <NavLink to={`detail/${item.restaurant.id}`} className="Link">
             <div className="img">
            <img src={item.restaurant.thumb}/>

            
             </div>
             <div className="info">
             <p>{this.state.data.id}</p>
             <section className="forthumb">
             <img src={item.restaurant.thumb} style={{width:"50px",height:"50px",borderRadius:"50px"}}/>
             </section>
             <section className="fordesc">
             <h2>{item.restaurant.name}</h2>
             <p >$$$ - {item.restaurant.location.city}</p>
             </section>
               
             </div>
             </NavLink>
             </div>
                )
               })}
             </div>

              : <p>"loading..."</p>}
             
        <div className="exploremore">
       <Link to=""><button className="exbutton">Explore more</button></Link>
        </div>     
            </div>

			)
	}
}
export default Restaurant